.mainimage {
  position: relative;
}

.imagelist {
  width: 100%;
}
.maintext {
  position: absolute;
  color: rgb(255, 1, 107);
  font-size: 4em;
  top: 50%;
  left: 50%;
  -ms-transform: translate(-50%, -50%);
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  margin: 0;
  padding: 0;
}

.wardimge {
  width: 300px;
  height: 300px;
  display: inline-block;
}
.wardtx {
  display: inline-block;
  vertical-align: top;
  margin-left: 20px;
}
